<template>
  <div id="main" style="width: 100%; height: 800px"></div>
</template>

<script>
import * as echarts from 'echarts';
import "@/js/ChinaMapchain.js";

export default {
  data() {
    return {
      tableData: [] // 用于存放JSON数据
    };
  },
  created() {
    // 在 created 钩子中发起 fetch 请求
    fetch("/assets/json/ChinaMapPoint.json")
      .then(response => response.json())
      .then(data => {
        // 处理数据并存储到 tableData 中
        this.tableData = data.map(item => ({
          name: item.name,
          value: item.value,
          sales: item.Sales
        }));

        // 输出 tableData 的内容
        console.log("tableData:", this.tableData);

        // 确保 DOM 更新完成后再初始化图表
        this.$nextTick(() => {
          this.initChart();
        });
      })
      .catch(error => {
        console.error("Error fetching JSON data:", error);
      });
  },
  methods: {
    initChart() {
      // 初始化 ECharts 实例
      const myChart = echarts.init(document.getElementById('main'));

      // 调用 dealWithData 函数，将 tableData 传入
      this.dealWithData(this.tableData, myChart);
    },
    dealWithData(data, chart) {
      // 图表配置代码和之前一致
      let dataValue = data;
      const option = {
        tooltip: {
          show: true,
          triggerOn: 'click'
        },
        geo: {
          map: 'china',
          roam: false,
          zoom: 1.2,
          center: [105, 36],
          itemStyle: {
            areaColor: '#f5f2f2',
            borderColor: "#835f5f",
          },
          label: {
            show: true,
            fontSize: "11.5",
            color: "rgb(107,102,102)"
          },
          emphasis: {
            itemStyle: {
              areaColor: '#d0a3a3',
            },
            label: {
              show: true,
              color: "rgb(255,255,255)"
            }
          }
        },
        series: [
          {
            name: '销量',
            type: 'scatter',
            coordinateSystem: 'geo',
            data: dataValue,
            symbolSize: 12,
            itemStyle: {
              color: "#f13434",
              shadowBlur: 2,
              shadowColor: "#333"
            },
            tooltip: {
              show: true,
              triggerOn: "click",
              formatter: function(data1) {
                let data2 = data1.data;
                return data2.sales;
              }
            },
          },
          {
            name: "产品销量",
            type: "effectScatter",
            coordinateSystem: "geo",
            data: dataValue,
            symbolSize: 4,
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke",
              color: "#f13434",
              period: 10,
              scale: 10
            },
            emphasis: {
              scale: true,
            },
            label: {
              formatter: "{b}",
              position: "top",
              show: true,
              fontSize: "10",
            },
            itemStyle: {
              color: "#f13434",
              shadowBlur: 2,
              shadowColor: "#333"
            },
            tooltip: {
              show: true,
              triggerOn: "click",
              formatter: function(data1) {
                let data2 = data1.data;
                return data2.sales;
              }
            },
            zlevel: 1
          }
        ]
      };

      // 使用配置项和数据显示图表
      chart.setOption(option);
    }
  }
};
</script>
